.gallery {
	min-height: rem(808);
	background-color: $bg-color;
	padding: rem(67) 0 rem(79) 0;
	@include laptop {
		min-height: 46rem;
	}
}

.gallery-video {
	width: 98%;
	height: 50%;
	position: relative;
	margin: 0 auto rem(34) auto;
}

.gallery-video__list {
	width: 82.5%;
	margin: 0 auto;
	@include desktop {
		width: 90%;
	}
	@include desktop_1190 {
		width: 72%;
	}
	@include laptop {
		width: 93%;
	}
	@include tablet {
		width: 82.5%;
	}
	@include tablet_610 {
		width: 90%;
	}
	@include mobile {
		width: 85%;
	}
}

.gallery-photo {
	width: 98%;
	height: 50%;
	position: relative;
	margin: 0 auto;
}

.gallery-photo__list {
	width: 82.5%;
	margin: 0 auto;
	@include desktop {
		width: 90%;
	}
	@include desktop_1190 {
		width: 72%;
	}
	@include laptop {
		width: 93%;
	}
	@include tablet {
		width: 82.5%;
	}
	@include tablet_610 {
		width: 90%;
	}
	@include mobile {
		width: 85%;
	}
}

.gallery-photo__img {
	max-width: 100%;
	height: auto;
}


